<template>
	<view class="container">
		<view class="statisBox">
			<view class="statis_item">
				<text>总套数</text>
				<text>{{current == 0 ? totalData.unSoldRoomCount : totalData.soldRoomCount}}</text>
			</view>
			<view class="statis_item">
				<text>总面积</text>
				<text>{{current == 0 ? totalData.unSoldRoomArea : totalData.soldRoomArea}}万m²</text>
			</view>
			<view class="statis_item">
				<text>总金额</text>
				<text v-if="current == 0">{{totalData.unSoldRoomMoney==-9999?'--':totalData.unSoldRoomMoney}}万元</text>
				<text v-else>{{totalData.soldRoomMoney==-9999?'--':totalData.soldRoomMoney}}万元</text>
			</view>
		</view>
		<view class="express_box">
			<view class="title">| 朝向</view>
			<u-table>
				<u-tr class="u-tr">
					<u-th class="u-th">房间结构</u-th>
					<u-th class="u-th">套数</u-th>
					<u-th class="u-th">面积(万m²)</u-th>
					<u-th class="u-th">金额(万元)</u-th>
				</u-tr>
				<u-tr class="u-tr" v-for="(item,index) in orientationData" :key="index">
					<u-td class="u-td">{{item.cateGoryValue}}</u-td>
					<u-td class="u-td">{{current==0?item.unSoldRoomCount:item.soldRoomCount}}</u-td>
					<u-td class="u-td">{{current==0?item.unSoldRoomArea:item.soldRoomArea}}</u-td>
					<u-td class="u-td" v-if="current==0">{{item.unSoldRoomMoney==-9999?'--':item.unSoldRoomMoney}}</u-td>
					<u-td class="u-td" v-else>{{item.soldRoomMoney==-9999?'--':item.soldRoomMoney}}</u-td>
				</u-tr>
			</u-table>
		</view>
		<view class="express_box">
			<view class="title">| 户型</view>
			<u-table>
				<u-tr class="u-tr">
					<u-th class="u-th">户型</u-th>
					<u-th class="u-th">套数</u-th>
					<u-th class="u-th">面积(万m²)</u-th>
					<u-th class="u-th">金额(万元)</u-th>
				</u-tr>
				<u-tr class="u-tr" v-for="(item,index) in houseTypeData" :key="index">
					<u-td class="u-td">{{item.cateGoryValue}}</u-td>
					<u-td class="u-td">{{current==0?item.unSoldRoomCount:item.soldRoomCount}}</u-td>
					<u-td class="u-td">{{current==0?item.unSoldRoomArea:item.soldRoomArea}}</u-td>
					<u-td class="u-td" v-if="current==0">{{item.unSoldRoomMoney==-9999?'--':item.unSoldRoomMoney}}</u-td>
					<u-td class="u-td" v-else>{{item.soldRoomMoney==-9999?'--':item.soldRoomMoney}}</u-td>
				</u-tr>
			</u-table>
		</view>
	</view>
</template>

<script>
	export default {
		props:['current', 'totalData', 'houseTypeData', 'orientationData'],
		data() {
			return {}
		}
	}
</script>

<style lang="scss">
	.statisBox{
		background-color: #fff;
		margin-top: 20rpx;
		display: flex;
		justify-content: center;
		padding: 30rpx;
		.statis_item{
			flex: 1;
			display: flex;
			flex-direction: column;
			align-items: center;
			border-right: 1rpx solid #f6f6f6;
			text:last-child{
				font-size: 32rpx;
				padding-top: 15rpx;
			}
		}
		.statis_item:last-child{
			border: none;
		}
	}
</style>
